import ButtonStatus from "components/atom/button/ButtonStatus";
import { Pagination } from "components/atom/pagination";
import CustomTable from "components/atom/table";
import { getLocalizedDateString, splice } from "components/utility";
import React, { useEffect, useState } from "react";
import { SORT_KEY } from "shared/constants";
import { Images } from "shared/images";
import { INVOICE_SERVICES } from "shared/tableData";

function InvoiceTable({
  setmultiplePayload,
  multiplepayload,
  navigateBranches = () => { },
  data,
  deleteBranch = () => { },
  itemsPerPage,
  setItemsPerPage,
  pageCount,
  setPageCount,
  setSortKey,
  sortKey,
  pageItems,
  setPageItems,
}) {
  const [multipleSelect, setMultipleSelect] = useState(false);

  const sortSwitchKey = (name) => {
    switch (name) {
      case INVOICE_SERVICES.CREATED_DATE:
        return setSortKey(
          sortKey === SORT_KEY.createdAt ? SORT_KEY.default : SORT_KEY.createdAt
        );
      case INVOICE_SERVICES.FROM:
        return setSortKey(
          sortKey === SORT_KEY.from ? SORT_KEY.default : SORT_KEY.from
        );
      case INVOICE_SERVICES.TO:
        return setSortKey(
          sortKey === SORT_KEY.to ? SORT_KEY.default : SORT_KEY.to
        );
      default:
        return setSortKey(SORT_KEY.default);
    }
  };

  const SortImage = ({ name }) => {
    switch (name) {
      case INVOICE_SERVICES.CREATED_DATE:
        return (
          <span className="sort_icon">
            <img src={Images.arrowupSort} alt="" width={10} />
          </span>
        );
      case INVOICE_SERVICES.FROM:
        return (
          <span className="sort_icon">
            <img src={Images.arrowupSort} alt="" />
          </span>
        );
      case INVOICE_SERVICES.TO:
        return (
          <span className="sort_icon">
            <img src={Images.arrowupSort} alt="" />
          </span>
        );
      default:
        return null;
    }
  };

  useEffect(() => {
    if (multiplepayload?.length === 0) setMultipleSelect(false);
  }, [multiplepayload]);

  return (
    <div className="tab_content">
      <CustomTable>
        <thead>
          <tr key={1000 + 1}>
            <th>
              <div className="checkbox">
                <input
                  type="checkbox"
                  //disabled={!multiplepayload.length}
                  checked={multipleSelect}
                  onChange={(e) => {
                    if (e.target.checked) {
                      setmultiplePayload(data.map((value, index) => value?.id));
                      setMultipleSelect(true);
                    } else if (data?.length > multiplepayload.length) {
                      setMultipleSelect(false);
                    } else {
                      setmultiplePayload([]);
                      setMultipleSelect(false);
                    }
                  }}
                />
                <label></label>
              </div>
            </th>
            {Object.values(INVOICE_SERVICES).map((i, idx) => (
              <th scope="col" id={idx} onClick={() => sortSwitchKey(i)}>
                {i}
                <SortImage name={i} />
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {data.length ? (
            data.map((i, idx) => {
              return (
                <>
                  <tr>
                    <td>
                      <div className="checkbox">
                        <input
                          type="checkbox"
                          checked={multiplepayload.includes(i?.id)}
                          onChange={(e) => {
                            if (e.target.checked) {
                              setmultiplePayload((prev) => [...prev, i?.id]);
                            } else {
                              setmultiplePayload(splice(multiplepayload, i?.id));
                            }
                          }}
                        />
                      </div>
                    </td>
                    <td>{idx + 1} </td>
                    <td>{i?.from_invoice} </td>
                    <td>{i?.to_invoice} </td>
                    <td>{getLocalizedDateString(i?.created_at)} </td>
                    <td>
                      <ButtonStatus
                        image={Images.viewIcon}
                        status={"default"}
                        title={"View Details"}
                        onClick={() => navigateBranches(i?.id)}
                      />
                    </td>
                  </tr>
                </>
              );
            })
          ) : (
            <tr>
              <td colSpan={6} className="text-center py-5">
                No Documents Uploaded
              </td>
            </tr>
          )}
        </tbody>
      </CustomTable>

      <Pagination
        items={pageItems}
        setPageCount={setPageCount}
        itemsPerPage={itemsPerPage}
        setItemsPerPage={setItemsPerPage}
      />
    </div>
  );
}

export default InvoiceTable;
